import React, { Component } from 'react'
import E from 'wangeditor'
import Header from '../../components/Header'
import './index.css'
import { message, Radio, Modal } from 'antd'
import { updateArticle, deleteTopcic } from '../../servers/index'
import Aside from '../../components/Aside/index'
import { meida_url } from '../../utils/utils'
import { host, getArticleDetail } from '../../servers/index'

export default class Release extends Component {
    constructor() {
        super()
        this.state = {
            content: "<div>默认值</div>",
            title: "",
            limit: 0,
            type: 0,
            contentText: "",
        }
    }
    UNSAFE_componentWillMount() {
        this.getArticle()
    }
    componentDidMount() {


    }
    createEdit(html) {
        const elem = this.editorElem; //获取editorElem盒子
        const editor = new E(elem)  //new 一个 editorElem富文本
        editor.customConfig.uploadFileName = 'upload_image' //置上传接口的文本流字段
        editor.customConfig.uploadImgServer = host + '/upload/image'//服务器接口地址
        editor.customConfig.uploadImgHeaders = {
            Authorization: localStorage.getItem('token') // 设置请求头
        }
        editor.customConfig.uploadImgHooks = {
            customInsert: function (insertImg, result, editor) {
                if (result.code === 200) {
                    const url = meida_url + result.data.url
                    insertImg(url)
                } else {
                    message.error(result.error)
                }

            }
        }
        editor.customConfig.onchange = (html) => {
            this.setState({ content: html, contentText: editor.txt.text() })
        }
        editor.create() //创建
        editor.txt.html(html)
        this.setState({ contentText: editor.txt.text() })

    }

    getArticle() {
        const topicId = this.props.match.params.topic_id
        const username = this.props.match.params.username
        getArticleDetail({ topic_id: topicId, is_update: true }, username).then(res => {
            if (res.code === 200) {
                let { content, limit, article_type, title, introduce } = res.data
                if (limit === 'public') {
                    limit = 0
                } else {
                    limit = 1
                }
                this.createEdit(content, introduce)
                this.setState({ content, limit, type: article_type, title })
            } else {
                message.error(res.error)
            }
        })

    }

    onSubmit() {
        let { title, limit, content, contentText, type } = this.state
        const topicId = this.props.match.params.topic_id
        if (limit === 0) {
            limit = "public"
        } else if (limit === 1) {
            limit = "private"
        }
        if (!title.trim()) {
            message.info('请输入标题！')
            return
        }
        if (type === 0) {
            message.info('请选择文章类型！')
            return
        }
        if (!content.trim()) {
            message.info('文章内容不能为空！')
        }
        const data = {
            id: topicId,
            title,
            category: 'tec',
            limit,
            type,
            content,
            content_text: contentText,
        }
        updateArticle(data).then(res => {
            if (res.code === 200) {
                message.success('修改成功')
            } else {
                message.error(res.error)
            }
        })

    }

    onChangeLimit(e) {
        this.setState({ limit: e.target.value })
    }
    onChangeType(e) {
        this.setState({ type: e.target.value })
    }


    //删除文章
    deleteArticle() {
        Modal.confirm({
            title: '确定要删除吗？',
            cancelText: '取消',
            okText: "确定",
            onOk: () => {
                const topicId = this.props.match.params.topic_id
                const username = this.props.match.params.username
                deleteTopcic({ topic_id: topicId }, username).then(res => {
                    if (res.code === 200) {
                        message.success('删除成功！')
                        this.props.history.replace(`/article/${username}`);
                    } else {
                        message.error(res.error)
                    }
                })
            }
        })
    }
    render() {
        const { type } = this.state
        return (
            <div>
                <Header {...this.props} />
                <div className="container">
                    <div className="public-header">
                        <span className="header-name">更新文章</span>
                        <span>不要轻易放弃。学习成长的路上，我们长路漫漫，只因学无止境。 </span>
                    </div>
                    <div className="row">
                        <div className="col-md-8" style={{ paddingLeft: 0, paddingRight: 0 }}>
                            <div className="card" style={{ marginTop: '20px' }}>
                                <div style={{ paddingTop: 20, paddingLeft: '10px', display: 'flex', justifyContent: 'space-between' }}>
                                    <div>
                                        <input className="artcle-title" id="author" type="text"
                                            value={this.state.title} onChange={(e) => this.setState({ title: e.target.value })} />
                                        <label htmlFor="author" style={{ marginLeft: 10 }}>文章标题</label>
                                    </div>
                                    <span className="delete-btn" onClick={this.deleteArticle.bind(this)}>删除</span>

                                </div>
                                <div style={{ marginBottom: 20, paddingLeft: 10, display: 'flex', alignItems: "center" }}>
                                    <span>权限：</span>
                                    <Radio.Group onChange={this.onChangeLimit.bind(this)} value={this.state.limit}>
                                        <Radio value={0}>公开</Radio>
                                        <Radio value={1}>个人</Radio>
                                    </Radio.Group>
                                </div>

                                <div style={{ marginBottom: 20, paddingLeft: 10, display: 'flex', alignItems: "center" }}>
                                    <span>类型：</span>
                                    <Radio.Group onChange={this.onChangeType.bind(this)} value={type}>
                                        <Radio value={1}>python</Radio>
                                        <Radio value={2}>javascript</Radio>
                                        <Radio value={3}>其他</Radio>
                                    </Radio.Group>
                                </div>


                                <div id="editor" style={{}}></div>
                                <div style={{ padding: '10px', paddingTop: 0 }}>
                                    <div ref={(editorElem) => this.editorElem = editorElem} className="editor"></div>
                                    <button className="btn btn-primary" type="button" onClick={this.onSubmit.bind(this)} style={{ marginTop: 20 }}>更新</button>
                                </div>
                            </div>
                        </div>

                        <div className="col-md-4" style={{marginTop:20}}>
                            <Aside hideRank={true} {...this.props} />
                        </div>
                    </div>





                </div>

            </div>
        )
    }
}